define('elosh-client/models/artwork', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var a = DS['default'].attr,
      b = DS['default'].belongsTo;

  exports['default'] = DS['default'].Model.extend({
    bookTitle: a('string'),
    categories: a(),
    description: a('string'),
    medium: a('string'),
    slug: a('string'),
    title: a('string'),

    image: b('imageObject'),
    thumbnail: b('imageObject')
  });

});