define('elosh-client/mixins/routes/scroll-to-top', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Mixin.create({

    beforeModel: function beforeModel() {
      Ember['default'].$('#app-window').scrollTop(0);
      this._super();
    }

  });

});